
import Image from 'next/image';
import { PageSectionTitle } from '@/components/page-section-title';
import { SectionWrapper } from '@/components/section-wrapper';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { Github } from 'lucide-react';

interface GitHubRepo {
  id: number;
  name: string;
  description: string | null;
  html_url: string;
  stargazers_count: number;
  forks_count: number;
  language: string | null;
}

// Fetches specific repositories from GitHub for a given user.
async function getFeaturedGithubProjects(username: string, repoNames: string[], token?: string): Promise<GitHubRepo[]> {
  if (!username || username === 'seu-usuario-padrao') {
    console.warn("GitHub username is not set. Please configure it.");
    return [];
  }
  if (repoNames.length === 0) {
    return [];
  }

  const headers: HeadersInit = {
    'Accept': 'application/vnd.github.v3+json',
  };
  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }

  const projects: GitHubRepo[] = [];
  for (const repoName of repoNames) {
    try {
      const response = await fetch(`https://api.github.com/repos/${username}/${repoName}`, { headers });
      if (!response.ok) {
        let errorMessage = `Error fetching project "${repoName}": ${response.status} ${response.statusText}. Check if the repository name is correct and public.`;
        if (response.status === 403) {
          errorMessage += ' You may have hit the GitHub API rate limit. Please provide a GITHUB_TOKEN environment variable to increase the rate limit.';
        }
        console.error(errorMessage);
        continue; // Skip to the next repo if one fails
      }
      const data = await response.json();
      projects.push(data as GitHubRepo);
    } catch (error) {
      console.error(`Failed to fetch project "${repoName}":`, error);
    }
  }
  return projects;
}

export async function PortfolioSection() {
  const githubUsername = 'viniciusperico';
  const githubToken = process.env.GITHUB_TOKEN;
  
  const featuredRepoNames = [
    'Site-Cafe-do-Urso',
    'ReciboCIS',
    'FlipClocker-Page',
    'GeradorQRCode',
    'CalculadoraJS',
    'LinkHub-app',
    'PageLinkHub-React',
  ]; 

  const projects = await getFeaturedGithubProjects(githubUsername, featuredRepoNames, githubToken);

  return (
    <SectionWrapper id="projetos">
      <PageSectionTitle
        title="Meus Projetos no GitHub"
      />
      {featuredRepoNames.length === 0 && (
        <div className="text-center text-foreground/70 bg-card/50 p-6 rounded-lg shadow">
          <p className="mb-2">Por favor, configure os repositórios que deseja exibir.</p>
          <p>Abra o arquivo <code>src/components/sections/portfolio-section.tsx</code> e adicione os nomes dos seus projetos na lista <code>featuredRepoNames</code>.</p>
        </div>
      )}
      {projects.length === 0 && featuredRepoNames.length > 0 && (
         <div className="text-center text-foreground/70 bg-card/50 p-6 rounded-lg shadow">
          <p>Nenhum projeto encontrado para os nomes fornecidos.</p>
          <p>Verifique se os nomes dos repositórios em <code>featuredRepoNames</code> estão corretos e se eles são públicos.</p>
        </div>
      )}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
        {projects.map((item) => (
          <Card key={item.id} className="overflow-hidden shadow-lg transition-all duration-300 hover:shadow-2xl hover:scale-105 flex flex-col bg-card/80 backdrop-blur-sm">
            <CardHeader className="p-0">
              <div className="relative aspect-video">
                <Image
                  src="https://placehold.co/600x400.png"
                  alt={`Imagem do projeto ${item.name}`}
                  layout="fill"
                  objectFit="cover"
                  data-ai-hint="software project abstract"
                />
              </div>
            </CardHeader>
            <CardContent className="pt-6 flex-grow">
              <CardTitle className="text-xl font-semibold text-primary mb-2 font-headline">{item.name}</CardTitle>
              <p className="text-foreground/80 text-sm leading-relaxed min-h-[5rem]">
                {item.description || 'Sem descrição disponível.'}
              </p>
            </CardContent>
            <CardFooter>
              <Button asChild variant="outline" className="w-full text-primary border-primary hover:bg-primary/10">
                <Link href={item.html_url} target="_blank" rel="noopener noreferrer">
                  Ver no GitHub <Github className="ml-2 h-4 w-4" />
                </Link>
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </SectionWrapper>
  );
}
